import { createApp } from 'vue'
import App from './App.vue'
import myHeader from './global/myHeader.vue'
import Home from './themes/Home.vue'
import Carts from './themes/Carts.vue'
import Crossplatform from './themes/Crossplatform.vue'
import DragDrop from './themes/DragDrop.vue'
import Powerfull from './themes/Powerfull.vue'
import Pricing from './themes/Pricing.vue'
import myFooter from './global/myFooter.vue'

var Site = createApp(App);
Site.component("myHeader", myHeader);
Site.component("home", Home);
Site.component("carts", Carts);
Site.component("crossplatform", Crossplatform);
Site.component("dragDrop", DragDrop);
Site.component("powerfull", Powerfull);
Site.component("pricing", Pricing);
Site.component("myFooter", myFooter);

Site.mount('#app');
